body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.content {
  min-height: calc(100vh - 70px -64px);
}

nav{
  background-color: rgb(127, 197, 255);
}
.page-footer{
  background-color: rgb(127, 197, 255);
}

.cart {
  position: fixed;
  bottom: 2rem;
  right: 2rem;
  cursor: pointer;
  border-radius: 5%;
  z-index: 5;
  padding: 1rem;
}
@media (min-width: 767px) {
  .cart {
    bottom: unset;
    top: 5rem;
  }
  .goods{
    margin-top: 2%;
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;
    grid-column-gap: 20px;
  }

}
.material-icons {
  font-size: 35px;
}
.basket-list {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: #fff;
  width: 50rem;
  max-width: 95%;
  box-shadow: 1px 3px 8px 5px #4dd0e1;
  animation: show 500ms ease-in-out;
}
@keyframes show{
  from {
    top: 70%;
    opacity: 0;
  }
  to {
    top: 50%;
    opacity: 1;
  }
}
.basket-clear{
  cursor: pointer;
  position: absolute;
  top: 0.5rem;
  right: 0.5rem;
  color:#fff;
}
.basket-quantity{
  cursor: pointer;
  vertical-align: middle;
  color: #4dd0e1;
  font-size: 20px;
}

#toast-container {
  top: 3rem !important;
  animation: fade-in 250ms ease-in-out;
  -webkit-animation: fade-in 250ms ease-in-out;
}
@keyframes fade-in{
  from {
    transform: translateY(2rem);
    opacity: 0;
    -webkit-transform: translateY(2rem);
    -moz-transform: translateY(2rem);
    -ms-transform: translateY(2rem);
    -o-transform: translateY(2rem);
  }
  to{
    transform: translateY(0rem);
    opacity: 1;
    -webkit-transform: translateY(0rem);
    -moz-transform: translateY(0rem);
    -ms-transform: translateY(0rem);
    -o-transform: translateY(0rem);
  }
}

.teal.lighten-2 {
  background-color: rgb(127, 197, 255) !important;
}

.btn, .btn-large, .btn-small {
  text-decoration: none;
  color: #fff;
  background-color: rgb(127, 197, 255);
  text-align: center;
  letter-spacing: .5px;
  -webkit-transition: background-color .2s ease-out;
  transition: background-color .2s ease-out;
  cursor: pointer;
}

.btn:hover{
  background-color: rgb(72, 144, 203) !important;
}
.btn:focus{
  background-color: rgb(72, 144, 203) !important;
}

.determinate {
  color: rgb(127, 197, 255) !important;
  background-color: rgb(127, 197, 255) !important;
}
.indeterminate {
  color: rgb(127, 197, 255) !important;
  background-color: rgb(127, 197, 255) !important;
}
.progress{
  color: rgb(127, 197, 255) !important;
  background-color: rgb(191, 226, 255) !important;
}

@media (max-width: 1024px){
  .goods{
    margin-top: 3%;
    display: block;
  }
}
.right{
  margin-top: 7px;
}
.collection .collection-item.active {
  background-color: rgb(127, 197, 255) !important;
  color: #eafaf9;
}
.basket-quantity {
  cursor: pointer;
  vertical-align: middle;
  color: rgb(72, 144, 203) !important;
  font-size: 20px;
}
.secondary-content {
  float: right;
  color: rgb(127, 197, 255) !important;
}
.basket-clear{
  margin-top: -3.5px;
  margin-right: 14px;
  cursor: pointer;
}
.basket-delete{
  cursor: pointer;
}